.main {
    max-width: 1700px;
    height: 780px;
    margin: 0 auto;
    background: url('../../assets/images/main-background.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;
    z-index: 2;
    font-size: 10px;
}

.main-container {
    width: 768px;
    position: relative;
    top: 31.1em;
    left: 14em;
    z-index: 4;
    display: flex;
    flex-flow: column nowrap;
    gap: 20px;
}

.main-container-heading {
    font-weight: 700;
    font-size: 6.2em;
    line-height: 124%;
    text-transform: uppercase;
    color: #FFFFFF;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.main-container-paragraph {
    width: 100%;
    font-weight: 400;
    font-size: 1.6em;
    line-height: 160%;
    color: #FFFFFF;
    opacity: 0.7;
    margin-bottom: 7px;
}



.bottom-border-layer {
    border: 1px solid;
    width: 100%;
    height: 10px;
    max-width: 1400px;
    
    bottom: -70;
}

@media only screen and (max-width: 991px) {
    .main {
        font-size: 8px;
    }
    .main-container {
        max-width: 70%;
        left: 9%;

    }

    .main-container-paragraph {
        max-width: 80%;
    }
  }

  .bottom-borderer {
      border: 4px solid red;
      position: relative;
      top: 4000;
  }

  .main-border {
    width: calc(100% - 20px);
    height: 10px;
    left: 0px;
    margin-left: 10px;
    margin-right: 10px;
    bottom: 0px;
    height: 10px;
    border: solid 2px solid red;
    background: #2B3252;
    position: absolute;
    bottom: -10px;
  }



@media only screen and (max-width: 768px) {
    .main {
        font-size: 7px;
    }
}

